/* user form end */

/* slick sliders */
// slider
.slick-slide {
	-webkit-tap-highlight-color: rgba(255, 255, 255, 0) !important;
	-webkit-focus-ring-color: rgba(255, 255, 255, 0) !important;
	outline: none !important; }

// arrows
.slick-arrow {
	@include size(40px);
	display: block;
	position: absolute;
	border: none;
	border-radius: 0;
	background: none;
	outline: none;
	box-shadow: none;
	opacity: 1;
	z-index: 6;
	transition-property: background, opacity, visibility;
	transition-duration: 0.2s;

	.no-touchevents &:hover {
		opacity: 1;
		background: #e6e6e6; }

	&.slick-disabled, .no-touchevents &.slick-disabled {
		opacity: 0.25;
		-ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
		filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
		cursor: default;

		&::before {
			opacity: 1; } }

	&::before {
		content: none; }

	&::after {
		top: 50%;
		content: "";
		position: absolute;
		opacity: 1;
		pointer-events: none;
		@include size(12px);
		transform: rotate(45deg);
		border: 0 solid #000; } }

.slick-prev {
	left: 0;

	&::after {
		left: 50%;
		border-width: 0 0 2px 2px;
		margin: -6px 0 0 -4px; } }

.slick-next {
	right: 0;

	&::after {
		right: 50%;
		border-width: 2px 2px 0 0;
		margin: -6px -4px 0 0; } }

// dots
.slick-dots {
	margin: 0;
	line-height: 0;
	pointer-events: none;
	bottom: -2.8rem;

	.#{$user-content} & {
		margin: 0;
		max-width: none; }

	li {
		vertical-align: top;
		width: auto;
		height: auto;
		margin: 0;
		pointer-events: auto;

		&, .#{$user-content} & {
			padding: 3px;
			margin: 0;

			&::before {
				content: none; } }

		button {
			width: 10px;
			height: 10px;
			padding: 0;
			border-radius: 50%;
			background: $text-color-light;
			position: relative;
			transition: background-color $animation-speed ease-in-out;

			&::before {
				content: none; } }

		&.slick-active button {
			background-color: $highlight-color; } } }

//.no-touchevents &:hover button::before
/* slick sliders end */

/* promo slider */
// slider
.info {
	margin-bottom: $bottom-space--element;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--element--mob; }

	&:last-child {
		margin-bottom: 0; } }

.info__slider {
	overflow: hidden;

	// only for mobile
	@include media('<mob-lg') {
		// before slide load
		white-space: nowrap;
		overflow: visible;
		opacity: 0;

		&.slick-initialized, .no-js & {
			// after slide load
			white-space: normal;
			opacity: 1; }

		.slick-list {
			overflow: visible;
			@include margin-horizontal(-#{round($info-slider-space / 2)}); } } // компенсировать отступы между слайдами

	// destroy slider for desktop
	@include media('>=mob-lg') {
		.slick-track {
				opacity: 1 !important;
				width: auto !important;
				transform: none !important; }

		.slick-slide {
			width: 100% !important;

			&.slick-cloned {
				display: none !important; } }

		.slick-dots {
			display: none !important; } }

	&, &.slick-dotted {
		margin: 0; }

	//&.slick-dotted
	//include media('<mob-lg')
 }	//margin-bottom: $bottom-space--element

.info__heads, .info__item .tr {
	@include clear-spacing;
	.td {
		$item-width: round-float(150 / 960 * 100%, 2);
		display: inline-block;
		vertical-align: top;
		white-space: normal;
		width: $item-width;

		@include media('<mob-lg') {
			width: round-float(100% / 2, 2); }

		&:first-child {
			position: relative;
			width: round-float(100% - ($item-width * 4), 2); } } }

.info__heads {
	@include padding-vertical(0.7rem, 1.5rem);

	@include media('<mob-lg') {
		display: none; }

	.td {
		@include font(1.6rem, round-float(24/16, 2));
		letter-spacing: $letter-spacing;
		color: rgba($text-color, 0.4); } }

// slide
.info__item {
	position: relative;
	border-top: 1px solid rgba($text-color, 0.2);
	color: inherit;
	margin-top: -1px;

	// only for mobile
	@include media('<mob-lg') {
		border: none;
		margin: 0;
		@include padding-horizontal(round($info-slider-space / 2)); // добавить отступы между слайдами
		// before slide load
		float: none;
		display: inline-block;
		vertical-align: top;
		width: 100%;

		.slick-initialized & {
			// after slide load
			float: left;
			display: block; } }

	.td {
		@include font-bold;
		@include font(2.6rem, round-float(26/26, 2));
		letter-spacing: $letter-spacing;
		color: inherit;

		@include media('<mob-lg') {
			@include font-medium;
			font-size: 2.0rem;
			padding-top: 1rem; }

		&::before {
			display: none;
			content: attr(data-label);
			@include font-normal;
			@include font(13px, 15px);
			color: $text-color-light;
			margin-bottom: 0.4em;

			@include media('<mob-lg') {
				display: block; } } }

	.tr {
		@include padding-vertical(2rem);

		@include media('<mob-lg') {
			padding: nth($grid-space--mob, 1) nth($grid-space--mob, 2) round-float(nth($grid-space--mob, 1) * 0.8, 2);
			background-color: $base-color; }

		.td.info__item__caption {
			@include media('<mob-lg') {
				display: block;
				width: auto;
				background-color: #fff;
				padding: nth($grid-space--mob, 1) nth($grid-space--mob, 2) round-float(nth($grid-space--mob, 1) * 0.8, 2);
				text-align: center;
				margin: -#{nth($grid-space--mob, 1)} -#{nth($grid-space--mob, 2)} 0.5rem;

				&::before {
					display: none; } } } } }

.info__item__img {
	position: relative;
	padding: 0;
	margin: 0 0 nth($page-layout-space--mob, 2);
	display: none;

	@include media('<mob-lg') {
		display: block; }

	&::before {
		content: "";
		display: block;
		@include ratio(210, 90); }

	img {
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		max-width: none;
		margin: 0 auto;
		@include object-fit(contain); } }

.info__item__title {
	@include font-bold;
	margin-bottom: 0.4em;

	@include media('<mob-lg') {
		@include font(2.0rem, round-float(22/20, 2));
		color: #242473;
		margin-bottom: 0.1em; } }

.info__item__release {
	color: $highlight-color;
	@include font-normal;
	@include font(1.4rem, round-float(18/14, 2));

	@include media('<mob-lg') {
		@include font-medium;
		font-size: 12px; } }

/* promo slider end */

/* breadcrumbs */
.breadcrumbs {
	margin-bottom: $bottom-space--section;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--section--mob; }

	&:last-child {
		margin-bottom: 0; } }

.breadcrumbs__list {
	&, .#{$user-content} & {
		padding: 0;
		margin: 0;
		list-style: none;

		li {
			padding: 0 6px 0 0;
			margin: 0;

			&::before, &:first-child::before {
				content: none; } } }

	& {
		cursor: default;
		text-align: center;
		@include font($font-size--small, $line-height--small); }

	.layout-article & {
		max-width: none; }

	li {
		display: inline;
		position: relative;

		&::after {
			display: inline-block;
			vertical-align: baseline;
			content: '/';
			border: none;
			color: $highlight-color;
			padding-left: 10px; }

		&:last-child::after {
			content: none; } }

	a {
		padding-top: 0.5em;
		color: $highlight-color;
		text-decoration: none;
		cursor: pointer;
		// transition
		$prop: color;
		transition: {
			property: $prop;
			duration: $animation-speed; }

		//span
		//order-bottom: 1px solid transparent
		/// transition
		//prop: border-color
		//ransition:
		//property: $prop
		//duration: $animation-speed

		&:hover {
			color: $highlight-color--hover;
			//text-decoration: underline

			//span
 } } }			//order-color: $highlight-color

/* breadcrumbs end */

/* social networks */
.social-networks {
	margin-bottom: $bottom-space;

	&:last-child {
		margin-bottom: 0; } }

.social-networks__list {
	position: relative;
	display: inline-block;
	vertical-align: top;
	margin: 0 -10px -20px 0; }

.social-networks__item {
	float: left;
	margin: 0 10px 20px;

	a {
		display: block;
		position: relative;
		color: $highlight-color;
		transition: {
			property: color, border-color;
			duration: $animation-speed; }

		span {
			@include hide-text; }

		svg {
			display: block;
			@include size(60px); }

		path {
			fill: currentColor; }

		// hover
		&:hover {
			color: $highlight-color--hover; } } }

/* social networks end */

/* social networks in footer */
.f-soc {
	overflow: hidden;
	margin-bottom: $bottom-space--element;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--element--mob; }

	&:last-child {
		margin-bottom: 0; } }

.f-soc__group {
	float: left;
	margin-left: nth($footer-box-space, 2);

	@include media('<desk-lg') {
		margin-left: nth($footer-box-space--md, 2); }

	@include media('<tab-lg') {
		margin-left: nth($footer-box-space--mob, 2); }

	&:first-child {
		margin-left: 0; }

	& > strong {
		display: block;
		margin-bottom: 5px;
		font-size: 14px;
		line-height: 18px;
		color: $text-color-light;

		@include media('<tab-lg') {
			font-size: 12px; } } }

.f-soc__list {
	position: relative;
	display: inline-block;
	vertical-align: top;
	margin: 0 -15px -15px 0;

	@include media('<tab-lg') {
		margin: 0 -10px -10px 0; } }

.f-soc__item {
	float: left;
	margin: 0 15px 15px 0;

	@include media('<tab-lg') {
		margin: 0 10px 10px 0; }

	a {
		display: block;
		position: relative;
		color: $highlight-color;
		transition: {
			property: color, border-color;
			duration: $animation-speed; }

		span {
			@include hide-text; }

		svg {
			display: block;
			@include size(60px);

			@include media('<tab-lg') {
				@include size(40px); } }

		path {
			fill: currentColor; }

		// hover
		&:hover {
			color: $highlight-color--hover; } } }

/* social networks in footer end */

/* main sections */
.main-section {
	overflow: hidden;
	@include margin-horizontal(-#{nth($page-layout-space, 2)});
	margin-bottom: $bottom-space--section;
	@include padding-horizontal(#{nth($page-layout-space, 2)});
	@include clear;

	@include media('<desk-lg') {
		@include margin-horizontal(-#{nth($page-layout-space--md, 2)});
		@include padding-horizontal(#{nth($page-layout-space--md, 2)}); }

	@include media('<tab-lg') {
		@include margin-horizontal(-#{nth($page-layout-space--mob, 2)});
		margin-bottom: $bottom-space--section--mob;
		@include padding-horizontal(#{nth($page-layout-space--mob, 2)}); }

	&:last-child {
		margin-bottom: 0; } }

.main-section__heading {
	float: left;
	width: $main-sect-heading-width;
	min-height: 50px;
	padding-right: 2rem;

	@include media('<desk-lg') {
		float: none;
		width: auto;
		min-height: 0;
		padding-right: 0;
		margin-bottom: $bottom-space--title; }

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--title--mob; }

	h2, h3, h4,
	.h2, .h3, .h4 {
		margin-bottom: $bottom-space--row;
		text-align: left;

		@include media('<desk-lg') {
			margin-bottom: 0; }

		&:last-child {
			margin-bottom: 0; } } }

.main-section__content {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;
	margin-left: $main-sect-heading-width;

	@include media('<desk-lg') {
		display: block;
		margin-left: 0; } }

.main-section__caption {
	order: 1;
	@include font(2.2rem, round-float(26/22, 2));
	max-width: 50%;
	margin-bottom: $bottom-space--element;

	@include media('<desk-lg') {
		max-width: none;
		order: unset; }

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--element--mob;
		font-size: 1.6rem; } }

.main-section__body {
	width: 100%;
	order: 3;

	@include media('<desk-lg') {
		order: unset;
		margin-bottom: 4.5rem; }

	&:last-child {
		margin-bottom: 0; }

	ul, ol, p {
		margin-top: 0;

		&:last-child {
			margin-bottom: 0; } } }

.main-section__foot {
	order: 2;
	margin-bottom: $bottom-space--element;

	@include media('<desk-lg') {
		order: unset;
		text-align: center;
		margin-bottom: 0; } }

/* main sections end */

/* subscription form */
.subs-form {
	margin-bottom: $bottom-space--element;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--element--mob; }

	&:last-child {
		margin-bottom: 0; }

	form {
		display: block; }

	.label-holder, label, .error-note, .success-note {
		font-size: 14px;
		@include font($form-label-font-size--small, $form-label-line-height--small);
		margin-bottom: $form-label-vsb--small;
		@include padding-horizontal($form-input-space-h--small); }

	.input-holder {
		&.error {
			&::before, &::after {
				opacity: 0; } }

		&.success {
			&::before, &::after {
				background-color: $base-color;
				right: 17px;
				opacity: 0; } } }

	input[type="email"], input[type="input"] {
		@include padding-horizontal($form-input-space-h--small); }

	button {
		display: block;
		position: absolute;
		right: 0;
		top: 0;
		@include size($form-input-height);
		margin: 0;
		border: none;
		box-shadow: none;
		background-color: $highlight-color;
		border-radius: round($form-input-height / 2);
		color: $form-bg-color;
		// transition
		$prop: color, background-color;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $animation-speed;
			timing-function: ease-in-out; }

		&:hover {
			background-color: $highlight-color--hover; }

		svg {
			$size: 24px;
			display: block;
			position: absolute;
			left: 50%;
			top: 50%;
			@include size($size);
			margin: -#{round($size / 2)} 0 0 -#{round($size / 2)};
			// transition
			$prop: opacity;
			will-change: $prop;
			transition: {
				property: $prop;
				duration: $animation-speed;
				timing-function: ease-in-out; }

			path {
				fill: currentColor; } } }

	// Form error
	.error ~ button {
		background-color: $form-error-color; }

	// Form complete
	.form-success {
		.success-note {
			display: block; }

		.input-holder {
			&.success {
				&::before, &::after {
					opacity: 1; } } }

		input[type="email"], input[type="input"] {
			&:not(:disabled) {
				background-color: #56a4a6;
				border-color: #56a4a6; } }

		button {
			svg {
				opacity: 0; } } } }

/* subscription form end */

/* footer list */
.f-list {
	list-style: none;
	padding: 0;
	margin: 0 0 $bottom-space--element;
	font-size: 2.0rem;
	line-height: round-float(24/20, 2);

	@include media('<tab-lg') {
		font-size: 1.6rem;
		margin: 0 0 $bottom-space--element--mob; }

	&:last-child {
		margin-bottom: 0; }

	li {
		margin-bottom: 1.5rem;

		@include media('<tab-lg') {
			margin-bottom: 0.9rem; }

		&:last-child {
			margin-bottom: 0; }

		& > div {
			margin-bottom: 1rem;

			&:last-child {
				margin-bottom: 0; } } }

	a {
		color: inherit;
		// transition
		transition: color $animation-speed ease-in-out;

		&:not([href*="javascript:;"]):hover {
			color: $link-color; }

		&[href^="tel:"] {
			b {
				@include font-bold;
				font-size: 24px; } } } }

/* footer list end */

/* requisites */
.req {
	font-size: 12px;
	line-height: 14px;
	color: $text-color-light;

	p {
		margin-bottom: 15px;

		&:last-child {
			margin-bottom: 0; }

		b {
			color: white; } } }

/* requisites end */

/* footer form */
.appeal-form {
	margin: 0 0 $bottom-space--element;

	@include media('<tab-lg') {
		margin: 0 0 $bottom-space--element--mob; }

	&:last-child {
		margin-bottom: 0; }

	button {
		display: block;
		width: 100%;

		@include media('<tab-sm') {
			@include push-auto;
			max-width: 320px; } } }

.appeal-form__cols {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;

	@include media('<tab-sm') {
		display: block; } }

.appeal-form__col {
	width: calc(50% - 4rem / 2);

	@include media('<tab-lg') {
		width: calc(50% - 2rem / 2); }

	@include media('<tab-sm') {
		width: auto;
		margin-bottom: $form-bottom-space; }

	&:last-child {
		margin-bottom: 0; } }

.appeal-form__msg {
	position: absolute;
	right: 0;
	top: 0;
	width: 50%;
	height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	background-color: rgba($highlight-color, 0.95);
	z-index: $z-footer;
	padding: $footer-box-space;
	opacity: 0;
	pointer-events: none;
	// transition
	$prop: opacity;
	will-change: $prop;
	transition: {
		property: $prop;
		duration: $animation-speed;
		timing-function: ease-in-out; }

	@include media('<desk-lg') {
		width: 100%;
		padding: $footer-box-space--md; }

	@include media('<desk-lg') {
		padding: $footer-box-space--mob; }

	.form-success & {
		opacity: 1;
		pointer-events: auto; } }

.appeal-form__msg__title {
	display: block;
	text-align: center;
	color: white;
	margin-bottom: $bottom-space--row;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--row--mob; }

	& {
		@include font-h1; } }

.appeal-form__msg__subtitle {
	display: block;
	max-width: 600px;

	@include font-h3($base-color); }

/* footer form end */

/* big button of contacts */
$btn-cont-size: 876px 880px;
$btn-cont-size--max: 956px 960px;
$scale: round-float(nth($btn-cont-size--max, 2) / nth($btn-cont-size, 2), 2);
a.btn-cont {
	display: block;
	overflow: hidden;
	position: absolute;
	right: 0;
	bottom: 0;
	@include size(round(nth($btn-cont-size--max, 1) / 2), round(nth($btn-cont-size--max, 2) / 2));
	text-align: left;
	@include font-bold;
	@include font(2.6rem, round-float(32/26, 2));
	color: white;
	z-index: -1;

	& > span {
		display: block;
		position: absolute;
		width: 330px;
		right: 5rem;
		bottom: 3.2rem; }

	svg {
		display: block;
		position: absolute;
		right: 5.5rem;
		bottom: 2.8rem;
		@include size(30px);
		margin-bottom: 0.3rem;
		animation-iteration-count: infinite;
		animation-duration: 1.5s;
		animation-fill-mode: both;
		//$prop: transform
		//will-change: $prop
		//transition:
		//roperty: $prop
		//uration: $animation-speed
		//iming-function: ease-in-out

		path {
			fill: currentColor; } }

	&::before {
		content: "";
		display: block;
		position: absolute;
		left: 100%;
		top: 100%;
		@include size(nth($btn-cont-size, 1), nth($btn-cont-size, 2));
		margin: -#{round(nth($btn-cont-size, 1) / 2)} 0 0 -#{round(nth($btn-cont-size, 2) / 2)};
		background-color: $highlight-color;
		border-radius: 50%;
		animation-iteration-count: infinite;
		animation-duration: 1.5s;
		animation-fill-mode: both;
		// transition
		$prop: background-color, transform;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $animation-speed;
			timing-function: ease-in-out; } }

	// hover
	.no-touchevents &:hover {
		svg {
			//$tX: 1rem
			//transform: translate($tX, 0)
			//transform: translateX($tX)
			animation-name: pulseArr; }

		&::before {
			background-color: $highlight-color--hover;
			animation-name: pulse; } } }

@keyframes pulse {

	0% {
		$scale: 1;
		transform: scale($scale);
		transform: scale3d($scale, $scale, 1); }

	33% {
		transform: scale($scale);
		transform: scale3d($scale, $scale, 1); }

	to {
		$scale: 1;
		transform: scale($scale);
		transform: scale3d($scale, $scale, 1); } }

@keyframes pulseArr {

	0% {
		$tX: 0;
		transform: translate($tX, 0);
		transform: translateX($tX); }

	33% {
		$tX: 1rem;
		transform: translate($tX, 0);
		transform: translateX($tX); }

	to {
		$tX: 0;
		transform: translate($tX, 0);
		transform: translateX($tX); } }

/* big button of contacts end */

/* news */
.news {
	margin-bottom: $bottom-space--article;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--article--mob; }

	&:last-child {
		margin-bottom: 0; }

	&.max-wrap-flood {
		@include media('<mob-lg') {
			max-width: 360px; } } }

.news__list {
	position: relative;
	overflow: hidden;
	@include clear-spacing; }

.news__item {
	display: inline-block;
	vertical-align: top;
	width: calc(100% / 2);
	@include font-light;
	@include font($font-size, $line-height);
	letter-spacing: $letter-spacing;
	background-color: white;
	box-shadow: inset 1px 0 0 #e7e7f5;

	@include media('<tab-lg') {
		@include font($font-size--mob, $line-height--mob); }

	@include media('<mob-lg') {
		width: 100%;
		box-shadow: inset 0 1px 0 #e7e7f5; }

	&.news__item__more, &.news__item_has-img,
	&.news__item__more + &, &.news__item_has-img + & {
		box-shadow: none; }

	&.news__item_wide {
		width: 100%; } }

.news__item__inner {
	position: relative;
	display: block;
	text-decoration: none;
	color: $base-color;
	// transition
	$prop: background-color, color;
	will-change: $prop;
	transition: {
		property: $prop;
		duration: $animation-speed;
		timing-function: ease-in-out; }

	.news__item__more & {
		background-color: $highlight-color;
		color: white;
		@include font-bold;
		@include font(3.2rem, round-float(38/32, 2));
		z-index: 5;

		@include media('<tab-lg') {
			font-size: 2.4rem; }

		& > span {
			position: absolute;
			left: nth($grid-space, 2);
			bottom: round-float(nth($grid-space, 2) * 0.8, 2);

			@include media('<tab-lg') {
				left: nth($grid-space--mob, 2);
				bottom: round-float(nth($grid-space--mob, 2) * 0.8, 2); } }

		svg {
			display: block;
			position: absolute;
			right: nth($grid-space, 2) - 0.6;
			bottom: round-float(nth($grid-space, 2) * 0.8, 2);
			@include size(30px);

			@include media('<tab-lg') {
				right: nth($grid-space--mob, 2) - 0.6;
				bottom: round-float(nth($grid-space--mob, 2) * 0.8, 2); }

			path {
				fill: currentColor; } } }

	&::before {
		content: "";
		display: block;
		@include ratio(540, 360);
		z-index: -1;

		.news__item_wide & {
			@include ratio(1080, 360); }

		// index page
		.main-section__body & {
			@include ratio(480, 360); }

		.main-section__body .news__item_wide & {
			@include ratio(960, 360); }

		&, .news__item_wide &, .main-section__body &, .main-section__body .news__item_wide & {
			@include media('<mob-lg') {
				//padding: 0
				//height: 203px
				@include ratio(280px, 203px); } } } }

.news__item__figure,
.news__item__body {
	display: block;
	position: absolute;
	left: 0;
	top: 0;
	@include size(100%);
	padding: 0;
	margin: 0; }


.news__item__figure {
	display: none;

	.news__item_has-img & {
		display: block;

		&::before {
			$bg-color: #41418b;
			content: "";
			display: block;
			position: absolute;
			left: 0;
			bottom: 0;
			@include size(100%);
			opacity: 0.8;
			@include linear-gradient(to top, rgba($bg-color, 1) 0%, rgba($bg-color, 0) 100%);
			z-index: 1;
			// transition
			will-change: opacity;
			transition: opacity $animation-speed ease-in-out; } }

	img {
		position: absolute;
		left: 0;
		top: 0;
		max-width: none;
		width: 100%;
		height: 100%;
		@include object-fit(cover); } }

.news__item__body {
	z-index: 5;
	padding: nth($grid-space, 1) nth($grid-space, 2) round-float(nth($grid-space, 2) * 0.85, 1);
	display: flex;
	flex-direction: column;
	align-items: flex-start;

	@include media('<tab-lg') {
		padding: nth($grid-space--mob, 1) nth($grid-space--mob, 2) round-float(nth($grid-space--mob, 2) * 0.85, 1); }

	.news__item_has-img & {
		justify-content: flex-end; }

	.news__item_wide & {
		text-align: center;
		justify-content: flex-start;
		align-items: center; }

	p {
		&:last-child {
			margin-bottom: 0; } } }

.news__item__title {
	@include font(3.2rem, round-float(38/32, 2));
	overflow: hidden;

	@include media('<tab-lg') {
		@include font(2.0rem, round-float(24/20, 2)); }

	.news__item_has-img & {
		@include font(2.4rem, round-float(29/24, 2));
		color: white;

		@include media('<tab-lg') {
			@include font(1.6rem, round-float(19/16, 2)); } }

	.news__item_wide & {
		@include font(4.8rem, round-float(48/48, 2));
		max-width: 780px;
		@include push-auto;

		@include media('<tab-lg') {
			max-width: 480px;
			@include font(3.0rem, round-float(32/30, 2)); }

		@include media('<mob-lg') {
			@include font(2.0rem, round-float(24/20, 2)); } } }

.btn-source {
	display: inline-block;
	vertical-align: top;
	padding: 4px 16px 5px;
	min-width: 120px;
	@include font-bold;
	@include font(16px, 24px);
	color: $highlight-color;
	background-color: $base-color;
	border-radius: 17px;

	.news__item__body & {
		//margin-bottom: 2.8rem
		margin-bottom: round-float(nth($grid-space, 1) * 0.7, 1);

		@include media('<tab-lg') {
			margin-bottom: round-float(nth($grid-space--mob, 1) * 0.7, 1); } }

	.news__item_wide & {
		//margin-bottom: 4.5rem
		margin-bottom: round-float(nth($grid-space, 1) * 1.125, 1);

		@include media('<tab-lg') {
			margin-bottom: round-float(nth($grid-space--mob, 1) * 1.125, 1); }

		@include media('<mob-lg') {
			margin-bottom: round-float(nth($grid-space--mob, 1) * 0.7, 1); } } }

// hover
.no-touchevents {
	.news__item__inner:hover {
		.news__item__figure {
			&::before {
				opacity: 1; } } }

	.news__item__more {
		.news__item__inner:hover {
			color: white;
			background-color: $highlight-color--hover; } } }

/* news end */
